import React from 'react'
import DataJuegos from '../data/dataJuegos.json'

export function Juegos(){
    function handleClick(event){
        //event.target.style.color = "red"
        event.target.classList.add("cambioColor")
    }
    return( 
        <section className='juegos'>
            {DataJuegos.map((detalle, index) =>{
                const img = "./images/" + detalle.id + ".jpg"
                return (
                    <div className='cardJuego' key={detalle.id} onClick={handleClick}>
                        <h2>{detalle.nombre}</h2>
                        <p>{detalle.desc}</p>
                        <img src={img} alt="{detalle.id}" />
                    </div>
                    )
                })
            }
        </section>
    )
}