import React from 'react'
import {useState, useEffect} from 'react'
import { Rana } from './demo/Rana.jsx'
import { Hoja } from './demo/Hoja.jsx'


export function Demo(){
        let state = true
        let [hoja10,setHoja10] = React.useState(false)
        function Cambiazo(event){
            console.log("dejameenpaz")
        }
        function Cambiazo2(event){
            setHoja10(true)
            event.target.classList.add("hidden")
            React.useEffect(() => console.log('holi'))
        }
        

    return(
        <div id="juego">
            <div className="parent">
                <div className="div1" onClick={Cambiazo}><Hoja/>{state ? <Rana/> : null}</div>
                
                <div className="div2" > </div>
                <div className="div3" onClick={Cambiazo}> <Hoja/></div>
                
                <div className="div4"> </div>
                
                <div className="div5"> </div>
                <div className="div6" onClick={Cambiazo}> <Hoja/> </div>
                <div className="div7"> </div>
                <div className="div8" onClick={Cambiazo}> <Hoja/> </div>
                
                <div className="div9" onClick={Cambiazo}> <Hoja/> </div>
                <div className="div10" onClick={Cambiazo}><Hoja/> </div>
                <div className="div11" onClick={Cambiazo}><Hoja/> </div>
                <div className="div12"> </div>
                
                <div className="div13"> </div>
                <div className="div14" onClick={Cambiazo}><Hoja/> </div>
                <div className="div15" onClick={Cambiazo}><Hoja/> </div>
                <div className="div16" onClick={Cambiazo2}><Hoja/>{hoja10 ? <Rana/> : null} </div>
                <div className="div17"> <Rana/> </div>
                
                {console.log(state)}
            </div>
        </div>
    )
}