import React from 'react'

export function Header(){
    //let header = document.getElementById('header')
    function createSquare(event){
        let div = document.createElement('div');
        div.innerHTML = document.getElementById('cuadrados').innerHTML;
        document.body.appendChild(div).classList.add("square");
    }
    return( 
        <header id="header">
            <img src="./images/logoheader.png" alt='Ttulo con las palabras "Rusty Lake" escritas en negro con un recuadro negro.' />
            <h5>A place to empty the mind... reflect on the past... the present... and the future...</h5>
            <button onClick={createSquare}>Create square</button>
            <div id="cuadrados"></div>
        </header>
    )
}